<template>
  <div>
    <NavBar />
    <h2 class="table-head">
      {{patientname}}'s Medical Record
    </h2>

    <table>
      <tr>
        <th>Date</th>
        <th>Age</th>
        <th>Gender</th>
        <th>Systolic Blood Pressure</th>
        <th>Diastolic Blood Pressure</th>
        <th>Body Temperature</th>
        <th>Pulse Rate</th>
        <th>Sugar level</th>
        <th>Weight</th>
        <th>Height</th>
        <th>result</th>
      </tr>
      <tr v-for="read in table" :key="read">
        <td>{{ read.date }}</td>
        <td>{{ read.age }}</td>
        <td>{{ read.gender }}</td>
        <td>{{ read.bloodtemp }}</td>
        <td>{{ read.bloodpressure }}</td>
        <td>{{ read.bodytemp }}</td>
        <td>{{ read.pulserate }}</td>
        <td>{{ read.sugarlevel }}</td>
        <td>{{ read.weight }}</td>
        <td>{{ read.height }}</td>
        <td>{{ read.result }}</td>
      </tr>
    </table>
  </div>
</template>

<script>
// import { component } from 'vue/types/umd'
 import axios from 'axios'

import NavBar from "./NavBar.vue";
import firebase from "firebase";

export default {
  name: "IndividualMedidata",
  components: {
    NavBar,
  },
  data() {
    return {
      table: {},
      user: "",
      patientname:JSON.parse(localStorage.getItem("username"))
    };
  },
  created() {
    this.getCurrentData();
    this.getMediData();
  },
  methods: {
    getMediData() {
      firebase
        .database()
        .ref(
          "Patients/History/" +
            JSON.parse(localStorage.getItem("username")) +
            "/"
        )
        .on("value", (snap) => {
          this.table = snap.val();
        });
    },

    getCurrentData(){
      firebase
        .database()
        .ref(
          "Patients/Medical-Data/" +
            JSON.parse(localStorage.getItem("username")) +
            "/"
        )
        .on("value", (snap) => {
          console.log(snap.val());
          var g=0;
          if(snap.val().gender == "male"){
            g=1;
          }
          var bmi= parseInt(snap.val().weight)/((parseInt(snap.val().height)/100)*(parseInt(snap.val().height)/100))
          console.log("bmi "+ bmi)
          var data = {
        Age: snap.val().age,
        Gender: g,
        Bodymass: bmi,
        SBP: snap.val().bloodtemp,
        BodyTemp: snap.val().bodytemp,
        Glucose: snap.val().sugarlevel,
        HeartRate: snap.val().pulserate,
        DBP: snap.val().bloodpressure,
        restecg: "1",
        Pregnancies: "0",
        cp: "1",
        fbs: "0",
      };
      axios.post("/sendData",data).then((res)=>{

          var response = "";
          var flag = 1;
          if (res.data.heartResult == "1") {
            flag = 0;
            response = "You have heart disease ,";
          }
          if (res.data.diabetesResult == "1") {
            flag = 0;
            response += "You have diabetes disease ,";
          }

          if (parseInt(data.BodyTemp) < 35 || parseInt(data.BodyTemp) > 37.8) {
            flag = 0;
            response += "Your body temperature is abnormal ,";
          }
          if (parseInt(data.Bodymass) < 18.5 || parseInt(data.Bodymass) > 25) {
            flag = 0;
            response +=
              parseInt(data.Bodymass) < 18.5
                ? "You are in the underweight range ,"
                : "You are in the overweight range ,";
          }
          //            console.log("38"<"220")
          if (parseInt(data.SBP) < 90 || parseInt(data.SBP) > 120) {
            flag = 0;
            response += "Your Blood pressure is abnormal ,";
          }
          if (parseInt(data.Glucose) > 100) {
            flag = 0;
            response += "Glucose level is abnormal ,";
          }
          if (
            parseInt(data.HeartRate) < 60 ||
            parseInt(data.HeartRate) > 100
          ) {
            flag = 0;
            response += "Heart rate is in abnormal range ,";
          }

          if (flag == 1) response = "Healthy person";
          else response += "Please consult Respective Doctor"


        firebase
        .database()
        .ref(
          "Patients/History/" + JSON.parse(localStorage.getItem("username")) + "/" + snap.val().date
        )
        .set({
          age: snap.val().age,
          gender: snap.val().gender,
          bloodtemp: snap.val().bloodtemp,
          bloodpressure: snap.val().bloodpressure,
          bodytemp: snap.val().bodytemp,
          pulserate: snap.val().pulserate,
          sugarlevel: snap.val().sugarlevel,
          height: snap.val().height,
          weight: snap.val().weight,
          date: snap.val().date,
          result: response,
        });

        this.getMediData();
      });
        });
    },

  },
};
</script>

<style scoped>
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}

table {
  padding: 300px;
  margin-top: 30px;
  border-collapse: collapse;
  border: 1.5px solid black;
  margin-left: auto;
  margin-right: auto;
}
th {
  padding: 10px;
}
td {
  padding: 5px;
}
.table-head {
  margin-top: 25px;
}
</style>